<template>
  <div id="sidebar">
    <avatar></avatar>

    <div class="icons">
      <router-link to="/note" title="笔记">
        <i class="iconfont icon-note"></i>
      </router-link>

      <router-link to="/notebooks">
        <i class="iconfont icon-notebook"></i>
      </router-link>
    </div>
    <div class="logout">
      <i class="iconfont icon-logout" @click="logout"></i>
    </div>
  </div>
</template>

<script>
import avatar from "@/components/Avatar";
import Auth from "@/apis/auth";
export default {
  components: {
    avatar
  },
  methods: {
    logout() {
      Auth.logout().then(data => {
        console.log(data);
        this.$router.push({ path: "/#" });
      });
    }
  }
};
</script>

<style lang="less" scoped>
#sidebar {
  position: relative;
  width: 56px;
  text-align: center;
  background-color: #2c333c;
  .icons {
    margin-top: 15px;
    a {
      padding: 6px 0;
      display: block;
    }

    .router-link-active {
      background: #5e6266;
    }
  }
  .logout {
    position: absolute;
    bottom: 20px;
    width: 100%;
    text-align: center;
    cursor: pointer;
  }
  .iconfont {
    color: #fff;
  }
}
</style>



